import React, {useRef} from 'react';
import './App.css'; // Import your CSS file
import SurpriseButton from './components/SurpriseButton';
import confetti from 'https://esm.run/canvas-confetti@1';

const Confetti = () => {
  confetti({
    particleCount: 150,
    spread: 60
  });
}
const BirthdayTimer = ({ birthday }) => {
  const calculateTimeLeft = () => {
    const difference = +new Date(birthday) - +new Date();
    let timeLeft = {};

    if (difference > 0) {
      timeLeft = {
        day: Math.floor(difference / (1000 * 60 * 60 * 24)),
        hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((difference / 1000 / 60) % 60),
        seconds: Math.floor((difference / 1000) % 60)
      };
    }

    return timeLeft;
  };

  const [timeLeft, setTimeLeft] = React.useState(calculateTimeLeft());

  React.useEffect(() => {
    const timer = setTimeout(() => {
      setTimeLeft(calculateTimeLeft());
    }, 1000);

    return () => clearTimeout(timer);
  });

  const timerComponents = [];

  Object.keys(timeLeft).forEach(interval => {
    if (!timeLeft[interval] && interval !== 'seconds') { // Check if interval is seconds
      return;
    }

    timerComponents.push(
      <div className='timer-container' key={interval}>
        <span className="timer-number">{interval === 'seconds' ? String(timeLeft[interval]).padStart(2, '0') : timeLeft[interval]}</span> <span className='timename'>{interval}{' '}</span>
      </div>
    );
  });

  return (
    <div>
      {timerComponents.length ? (
        <div className='timer-div'>
          <h1 className='timer-heading'>Darla ko birthday</h1>
          <h2>{timerComponents}</h2>
        </div>
      ) : 
      
      /************************************************
       *Esma Birthday wana component rakhne ↓↓↓ *******
       ************************************************/
      (
        <h1>Happy Birthday!</h1>
      )}
    </div>
  );
};

const App = () => {
  const girlfriendBirthday = '2024-05-06';
  return (
    <>
      <div className='main-container'>
        <BirthdayTimer birthday={girlfriendBirthday} />
        <div className='button-container'>
          <img src={require('./assets/img/dumpling-hands.png')} className='dumpling-hands' alt="Dumpling's hands"/>
          <SurpriseButton/>
          <img src={require('./assets/img/dumpling.png')} className='dumpling-img' alt='dumpling'/>
        </div>
      </div>
    </>
  );
};

export default App;
